import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import CarruselView from '../views/CarruselView.vue'
import PaisajeView from '../views/PaisajeView.vue'
import SepiaView from '../views/SepiaView.vue'
import KidsView from '../views/KidsView.vue'
import KidsGallery from '../views/KidsGallery.vue'
import SepiaGallery from '../views/SepiaGallery.vue'
import AboutGallery from '../views/AboutGallery.vue'
import HouseView from '../views/HouseView.vue'
import HouseGallery from '../views/HouseGallery.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/carrusel',
    name: 'carrusel',
    component: CarruselView
  },
  {
    path: '/paisaje',
    name: 'paisaje',
    component: PaisajeView
  },
  {
    path: '/sepia',
    name: 'sepia',
    component: SepiaView
  },
  {
    path: '/kids',
    name: 'kids',
    component: KidsView
  },
  {
    path: '/kidsgallery',
    name: 'kidsgallery',
    component: KidsGallery
  },
  {
    path: '/sepiagallery',
    name: 'sepiagallery',
    component: SepiaGallery
  },
  {
    path: '/aboutgallery',
    name: 'aboutgallery',
    component: AboutGallery
  },
  {
    path: '/house',
    name: 'house',
    component: HouseView
  },
  {
    path: '/housegallery',
    name: 'housegallery',
    component: HouseGallery
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
